
/**
 * @imports
 */
import _all from '../arr/all.js';
import _isFunction from './isFunction.js';
import _isObject from './isObject.js';

/**
 * Tells if a given object has observability methods.
 *
 * @param mixed		input
 *
 * @return bool
 */
export default function(input) {
	return _isObject(input) && _all([
		'observe',
		'unobserve',
		'get',
		'set',
		'has',
		'del',
	], method => _isFunction(input[method]));
};
